body {
    box-sizing: border-box;
    background-color: rgb(24, 24, 24);
    margin: 10px 150px;
}

.page {
    height: 100vh;
}

h1, h2, h3, a, p {
    color: white;
    text-decoration: none;
    font-family: 'Noto Serif TC', serif;
}

.title {
    text-align: center;
    font-size: 50px;
}

.textalign {
    text-align: center;
}

.center {
    display: block;
    margin: auto;
}

.flex {
    display: flex;
    flex-wrap: wrap;
}

.intro {
    width: 70%;
    margin: auto;
    padding: 70px 0px;
}


/* 首頁 */
header {
    text-align: center;
    color: rgb(255, 255, 255);
}

ul {
    display: flex;
    justify-content: space-evenly;
    padding: 0px;
}

li {
    list-style:none;
}

a:hover {
    text-decoration: underline;
}

.container {
    margin: 0px 0px 0px 100px;
    width: 90%;
    display:flex;
}


.img1 {
    transform: rotate(355deg);
    width: 900px;
}

.img2 {
    top: 40px;
    right: 160px;
    width: 300px;
}

/* 讀書會介紹 */

.page2 {
    height: 100vh;
    margin: 100px 0px;
}

.books {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: auto;
}


/* 關於 */
.about h2{
    text-align: flex-start;
    margin: 50px 10px;
}

.about {
    margin-left: 100px;
    margin-right: 100px;
}


.toggle-post {
	background: #b3b3b3;
	padding: 10px;
    margin: 10px;
	border-radius: 5px;
	width: 80%;
	cursor: pointer;
}

.toggle-post:hover {
	background: #d6d6d6;
}

.toggle-post .header {
	font-size: 18px;
	font-weight: 700;
	font-family: monospace;
}

.toggle-post .header i {
	transform: rotate(180deg); /* 反轉 180 度 */
	transition: .3s; /* Css 轉換時間, 這邊從 180deg 到 0 deg 會花 0.3 秒 */
}

.toggle-post.active .header i {
	transform: rotate(0deg); /* 反轉 0 度 */
}

.toggle-post .body {
	height: 0; /* 高度為 0 */
	overflow: hidden; /* 如果超過高度以外的字, 把他隱藏, 可以把這一行關掉試試 */
}

.toggle-post.active .body {
	height: auto; /* 自動調整高度 */
}



/* 文章 */

.article {
    margin: 200px 0px;
}

.gallerycontainer {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.gallery {
    width: 40%;
    margin: auto;
    padding: 100px 10px;
    overflow: hidden;
    vertical-align: middle;
}


/* Podcast */

.podcast {
    margin: 40px;
    padding: 10px;
    width: 60%;
}

.introduce {
    width: 30%;
    margin: auto;
    padding: 0px;
}

.process {
    width: 10%;
    margin: auto;
    padding: 70px 50px;
}


/*footer*/

footer {
    background-color: grey;
    height: 150px;
    margin: 0px -150px;
}

#icon {
    font-size: 40px;
}




